<template>
	<div class="cont-container">
		<div class="banner">
			<div class="item">
				<img src="../../static/images/cont_ban01.png" alt="">
			</div>
			<div class="item">
				<img src="../../static/images/cont_ban02.png" alt="">
			</div>
			<div class="item">
				<img src="../../static/images/cont_ban03.png" alt="">
			</div>
			<div class="item">
				<img src="../../static/images/cont_ban04.png" alt="">
			</div>
			<div class="item">
				<img src="../../static/images/cont_ban05.png" alt="">
			</div>
			<div class="item">
				<img src="../../static/images/cont_ban06.png" alt="">
			</div>
		</div>
		<div class="cont">
			<div class="title">
				<h3>联系我们</h3>
				<p>Contact Us</p>
			</div>
			<div class="addr_map" id="dituContent">
				
			</div>
			<div class="info">
				<div class="ct">地址：湖北省武汉市武昌区水果湖街东湖路10号水果湖广场A座9层1号-8号</div>
				<div class="ct">联系方式：4009020929</div>
			</div>
			<div class="cus_cont">
				<div class="item">
					<input type="text" name="name" placeholder="请输入邮箱">
				</div>
				<div class="item">
					<input type="text" name="name" placeholder="请输入联系内容">
				</div>
				<div class="item">
					<button class="sub">提交</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>

export default {
  name: 'Cont',
  data () {
    return {
      msg: 'Cont'
    }
  },
  mounted: function(){
  	this.$nextTick(function () {
  		//创建和初始化地图函数：
	    function initMap(){
	        createMap();//创建地图
	        setMapEvent();//设置地图事件
	        addMapControl();//向地图添加控件
	        addMarker();//向地图中添加marker
	    }
	    
	    //创建地图函数：
	    function createMap(){
	        var map = new BMap.Map("dituContent");//在百度地图容器中创建一个地图
	        var point = new BMap.Point(114.311586,30.598467);//定义一个中心点坐标
	        map.centerAndZoom(point,12);//设定地图的中心点和坐标并将地图显示在地图容器中
	        window.map = map;//将map变量存储在全局
	    }
	    
	    //地图事件设置函数：
	    function setMapEvent(){
	        map.enableDragging();//启用地图拖拽事件，默认启用(可不写)
	        map.enableScrollWheelZoom();//启用地图滚轮放大缩小
	        map.enableDoubleClickZoom();//启用鼠标双击放大，默认启用(可不写)
	        map.enableKeyboard();//启用键盘上下左右键移动地图
	    }
	    
	    //地图控件添加函数：
	    function addMapControl(){
	        //向地图中添加缩放控件
		var ctrl_nav = new BMap.NavigationControl({anchor:BMAP_ANCHOR_TOP_LEFT,type:BMAP_NAVIGATION_CONTROL_LARGE});
		map.addControl(ctrl_nav);
	        //向地图中添加缩略图控件
		var ctrl_ove = new BMap.OverviewMapControl({anchor:BMAP_ANCHOR_BOTTOM_RIGHT,isOpen:1});
		map.addControl(ctrl_ove);
	        //向地图中添加比例尺控件
		var ctrl_sca = new BMap.ScaleControl({anchor:BMAP_ANCHOR_BOTTOM_LEFT});
		map.addControl(ctrl_sca);
	    }
	    
	    //标注点数组
	    var markerArr = [{title:"武汉市",content:"我的备注",point:"114.307274|30.60344",isOpen:0,icon:{w:23,h:25,l:46,t:21,x:9,lb:12}}
			 ];
	    //创建marker
	    function addMarker(){
	        for(var i=0;i<markerArr.length;i++){
	            var json = markerArr[i];
	            var p0 = json.point.split("|")[0];
	            var p1 = json.point.split("|")[1];
	            var point = new BMap.Point(p0,p1);
				var iconImg = createIcon(json.icon);
	            var marker = new BMap.Marker(point,{icon:iconImg});
				var iw = createInfoWindow(i);
				var label = new BMap.Label(json.title,{"offset":new BMap.Size(json.icon.lb-json.icon.x+10,-20)});
				marker.setLabel(label);
	            map.addOverlay(marker);
	            label.setStyle({
	                        borderColor:"#808080",
	                        color:"#333",
	                        cursor:"pointer"
	            });
				
				(function(){
					var index = i;
					var _iw = createInfoWindow(i);
					var _marker = marker;
					_marker.addEventListener("click",function(){
					    this.openInfoWindow(_iw);
				    });
				    _iw.addEventListener("open",function(){
					    _marker.getLabel().hide();
				    })
				    _iw.addEventListener("close",function(){
					    _marker.getLabel().show();
				    })
					label.addEventListener("click",function(){
					    _marker.openInfoWindow(_iw);
				    })
					if(!!json.isOpen){
						label.hide();
						_marker.openInfoWindow(_iw);
					}
				})()
	        }
	    }
	    //创建InfoWindow
	    function createInfoWindow(i){
	        var json = markerArr[i];
	        var iw = new BMap.InfoWindow("<b class='iw_poi_title' title='" + json.title + "'>" + json.title + "</b><div class='iw_poi_content'>"+json.content+"</div>");
	        return iw;
	    }
	    //创建一个Icon
	    function createIcon(json){
	        var icon = new BMap.Icon("http://app.baidu.com/map/images/us_mk_icon.png", new BMap.Size(json.w,json.h),{imageOffset: new BMap.Size(-json.l,-json.t),infoWindowOffset:new BMap.Size(json.lb+5,1),offset:new BMap.Size(json.x,json.h)})
	        return icon;
	    }
	    
	    initMap();//创建和初始化地图
  	});
  }
}
</script>


<style scoped lang="less">
.cont-container{
	min-height: 600px;
	.banner{
		display: flex;
		.item{
			flex: 1;
		}
	}
	.cont{
		width: 1200px;
		margin: 0 auto;
		padding: 90px 0 80px 0;
		.title{
			margin-bottom: 40px;
			h3{
				line-height: 34px;
				font-size: 30px;
				margin-bottom: 16px;
				font-weight: normal;
			}
			p{
				line-height: 24px;
				font-size: 20px;
			}
		}
		.addr_map{
			min-height: 556px;
			border: 4px solid #ef871a;
			margin-bottom: 94px;
		}
		.info{
			margin-bottom: 60px;
			.ct{
				position: relative;
				height: 30px;
				line-height: 30px;
				font-size: 24px;
				padding-left: 36px;
				margin-bottom: 24px;
				&:before{
					position: absolute;
					top: 0;
					left: 0;
					display: block;
					content: '';
					width: 30px;
					height: 30px;
					background-color: #ef871a;
				}
			}
		}
		.cus_cont{
			padding-bottom: 80px;
			.item{
				height: 64px;
				background-color: #3d7a0d;
				margin-bottom: 18px;
				input[type='text'],
				button{
					width: 100%;
					height: 64px;
					line-height: 64px;
					text-align: center;
					font-size: 18px;
					color: #fff;
					background-color: transparent;
				}
			}
		}
	}
}
</style>


